.about#about {
  background-image: url('../../img/bg-liquid.jpg'), url("../../img/br-triangle.svg");
  background-repeat: no-repeat;
  background-position: top left, bottom right;
  &__wrap {
    @include r(xl) {
      margin-left: col-w(3/12); } } }

.people {
  text-align: center;
  font-size: 18px;
  &__photo {
    margin-bottom: 1em; }
  &__name {
    font-weight: bold; } }

.about-company {
  border: 2px solid #c3c3c3;
  padding: $lg 0;
  border-width: 2px 0;
  display: flex;
  @include rmax(sm) {
    flex-direction: column; }
  &__people {
    flex-shrink: 0;
    margin-right: 60px;
    margin-bottom: 30px;
    @include rmax(sm) {
      align-self: flex-start; } }
  &__txt {
    font-size: 14px;
    font-weight: bold;
    font-style: italic;
    color: #494848;
    padding-left: 50px;
    background: url("../../img/quote.svg") no-repeat top left;
    @include r(md) {
      line-height: 1.8;
      font-size: 20px; } } }

