.page-overflow {
  overflow-x: hidden;
  overflow-y: auto;
  background-color: #fff; }

.page {
  display: flex;
  width: 100%;
  min-height: 100vh;
  flex-direction: column; }

.page__content {
  flex: 1 0 auto; }

.container {
  margin-left: auto;
  margin-right: auto;
  max-width: 1280px;
  width: 100%;
  padding-left: 20px;
  padding-right: 20px; }

.section {
  padding: 2*$lg 0;
  @include r(md) {
    padding: 4*$lg 0; } }

.spacer {
  margin-bottom: 6*$lg; }

.row {
  @include row(); }

.navbar {
  background-color: rgba($c-main, .9);
  padding: 20px 0;
  color: #fff;
  position: sticky;
  width: 100%;
  top: 0;
  z-index: 1000;
  transition: transform .3s ease-in-out;
  &.mh-unpinned {
    transform: translateY(-100%); }
  &__wrap {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between; }
  &__logo {
    flex: 0 0 120px;
    margin-right: $lg;
    @include rmax(md) {
      flex: 0 0 90px; } }
  &__slug {
    font-size: 12px;
    font-weight: bold;
    @include rmax(lg) {
      display: none; } }
  &__contact {
    margin: 0 auto;
    display: flex;
    align-items: center; }
  &__tel {
    margin-right: 20px;
    @include rmax(sm) {
      display: none; }
    @include r(lg) {
      font-size: 24px; } }
  &__call {
    margin-left: 20px;
    @include rmax(lg) {
      display: none; } }
  &__work-time {
    @include rmax(xl) {
      display: none; } } }

.footer-page {
  background-color: $c-main;
  color: #fff;
  padding: 20px 0;
  font-size: 14px;
  &__wrap {
    display: flex;
    flex-wrap: wrap;
    @include rmax(sm) {
      flex-direction: column; } }
  &__logo {
    width: 90px;
    margin-right: 2*$lg;
    img {
      max-width: 100px; }
    @include rmax(sm) {
      margin: 0 0 20px;
      width: 100%; } }
  &__address {
    display: flex;
    margin-bottom: 20px;
    div:not(:first-child) {
      margin-left: 20px; } }
  &__tel {
    margin-left: auto;
    font-size: 18px;
    line-height: 2;
    @include rmax(md) {
      margin: auto;
      align-self: center; } }
  &__nav {
    padding: 20px;
    flex: 0 0 100%;
    text-align: center;
    .menu__item {
      display: inline-block;
      margin: 20px 5px 10px 5px; } }
  &__desc-warn {
    flex: 0 0 100%;
    text-align: center;
    color: $c-brand;
    font-size: 12px;
    margin-top: 10px; } }
