html {
  height: 100%;
}

body {
  font: 16px/1.4 "Roboto", sans-serif;
  color: #000;
  margin: 0;
  min-height: 100vh;
  text-rendering: optimizeSpeed;
}

audio,
canvas,
img,
video {
  max-width: 100%;
  height: auto;
  box-sizing: border-box;
  vertical-align: top;
}

img {
  border-style: none;
  outline: none !important;
}

a {
  color: inherit;
}

a:hover {
  color: inherit;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

ul[class],
ol[class] {
  list-style: none;
  margin: 0;
  padding: 0;
}

article > * + * {
  margin-top: 1em;
}

input,
button,
textarea,
select {
  font: inherit;
}

svg:not(:root) {
  overflow: hidden;
}

svg {
  fill: currentcolor;
}

.link-block {
  text-decoration: none;
  color: inherit;
}

.hidden {
  display: none;
}

.disable {
  opacity: 0.5;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}

.hide-text {
  text-indent: 100%;
  white-space: nowrap;
  overflow: hidden;
}

.svg-icon {
  display: inline-block;
}

