.prod-table th {
  font-size: 12px;
  background-color: #C3D5DC;
}

.prod-table__name {
  font-size: 1.1em;
  font-weight: bold;
  color: #4c5b82;
}

.prod-table__price {
  color: #4c5b82;
  font-size: 22px;
  font-weight: 900;
}

.prod-table__price span {
  font-size: .7em;
}

.slick-arrow {
  display: block;
  font-size: 0;
  color: transparent;
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  border: none;
  padding: 0px;
  appearance: none;
  width: 30px;
  height: 30px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1;
  outline: none;
  cursor: pointer;
  transition: all .5s;
}

.slick-prev {
  left: 20px;
  background-image: url("../img/arrow-prev.svg");
}

.slick-next {
  right: 20px;
  background-image: url("../img/arrow-next.svg");
}

.slick-disabled {
  opacity: .5;
}

@media (max-width: 780px) {
  .septic__item:not(:first-of-type) {
    margin-top: 60px;
  }
}

@media (min-width: 780px) {
  .septic__item {
    margin-left: 15px;
    margin-right: 15px;
    flex: 0 0 auto;
    width: calc(50% - 30px);
  }
}

.sept-count {
  width: 290px;
  margin: auto;
  border-radius: 60px;
  overflow: hidden;
  margin-bottom: 30px;
}

.sept-count__item {
  justify-content: center;
  padding: 10px 40px;
  height: 50px;
  align-items: center;
  color: #fff;
  background-color: #2d3238;
}

.sept-count__item .ico {
  font-size: 2em;
  margin-right: .5em;
}

.sept-count .slick-arrow {
  border-radius: 50%;
  border: 2px solid #fea724;
  background-color: #FFD185;
  width: 50px;
  height: 50px;
  background-size: 60%;
}

.sept-count .slick-disabled {
  opacity: .7;
}

.sept-count .slick-prev {
  left: 0;
  background-image: url("../img/minus.svg");
}

.sept-count .slick-next {
  right: 0;
  background-image: url("../img/plus.svg");
}

.slick-initialized .sept-count__item {
  display: flex;
}

.js-features .slick-prev {
  left: 0;
}

.js-features .slick-next {
  right: 0;
}

@media (max-width: 560px) {
  .js-features {
    padding: 0 30px;
  }
  .js-features .slick-slide {
    margin-bottom: 0;
  }
}
